import React, {Component} from 'react';
import {connect} from 'react-redux';
import BreadCrumbs from '../helpers/breadCrumbs';
import Label from '../helpers/label'
import searchIcon from "../../../images/users/ic_search@3x.png";
import {DataTable} from "primereact/datatable";
import {Column} from "primereact/column";
import deleteIcon from "../../../images/users/ic_delete@3x.png";
import *  as actions from '../../../store/actions/index'
import * as moment from "moment";
import {Paginator} from "primereact/paginator";
import {Dialog} from "primereact/dialog";
import userLabel from "../../../images/users/ic_amountUsers@3x.png";

class Subscribed extends Component {

    constructor( props ) {
        super( props );
        this.state = {
            deleteBride :'',
            deleteBrideVisible:false,
            goToPage:1,
            params:{
                supplierType:'',
                page:1,
                searchText:''
            }
        };
    }

    componentDidMount( ) {
        this.props.getSubscribed({ page:1 } );
    }

    componentWillUnmount( ) {
        this.props.initSbscribed({
            data:[],
            amount:0
        })
    }

    handleRemove = ( el, event ) => {
        this.setState({
            deleteBride :el._id ,
            deleteBrideVisible:true,
        })
    };
    removeSell = ( rowData, column ) => {
        return (
            <div className='remove_icon' onClick={this.handleRemove.bind(null, rowData)}>
                <img src={deleteIcon}/>
            </div>
        )
    };
    getDateRegisterSell=( rowData )=>{
        const date =  moment( rowData.registerDate  ).format('DD.MM.Y');
        return <div>{  date } </div>
    };
    returnParams = (  )=>{
        const params = {
            page :this.state.params.page
        };
        if( this.state.params.searchText!=='' ){
            params.searchText = this.state.params.searchText
        }

        if( this.state.params.supplierType!=='' ){
            params.supplierType = this.state.params.supplierType
        }
        return params
    };
    render() {
        const items = [
            {label: 'Users'},
            {label: 'Subscribed'},
        ];
        const PagesCount = Math.floor(this.props.subscribed.count / 10) + 1;
        const data = this.props.subscribed.data;
        const amount  = this.props.subscribed.amount;
        return (
            <section className='users__bridge-section sub_section'>
                <BreadCrumbs>
                    {items}
                </BreadCrumbs>
                <div className="bride_container column_container flex-start">
                    <section className='label'>
                        <div className='up_label row_conatiner' onClick={this.props.toExel}>
                            <div className='image'>
                                <img src={userLabel}/>
                            </div>
                            <div className='text'>Export Users</div>
                            <div className='count'>{amount}</div>
                        </div>
                        <div className='additional_contents'>
                            {this.props.children}
                        </div>
                    </section>
                    <div className='search'>
                        <img src={ searchIcon } className='search_icon'/>
                        <input value={ this.state.params.searchText } onChange={ async el => {
                            await  this.setState({
                                first: 0,
                                params:{
                                    ...this.state.params,
                                    searchText:el.target.value,
                                    page:1,

                                }
                            });
                            const params  = this.returnParams();
                            this.props.getSubscribed( params );
                        }}  placeholder='Search'/>
                    </div>
                    <DataTable  className='custom_table' value={data}>
                        <Column field="name" header="User Name" sortable={true}/>
                        <Column field="registerDate" body={this.getDateRegisterSell} header="Register Date" sortable={true}/>
                        <Column field="email" header="Email" sortable={true}/>
                    </DataTable>
                    <div className='paginator-container'>
                        <Paginator className='custom_paginator' rows={ 10 } totalRecords={ this.props.subscribed.count }
                                   first={ this.state.first }
                                   onPageChange={ async ( e ) => {
                                       await this.setState({
                                           first:e.first,
                                           goToPage:e.page + 1,
                                           params: {
                                               ...this.state.params,
                                               page: e.page + 1
                                           }
                                       });
                                       this.props.getSubscribed( this.returnParams( this.state ) )
                                   }}/>
                        <div className='right-paginator'>
                            <div className='text'>Go to page</div>
                            <input onChange={async el => {

                                let value  = el.target.value ;
                                await this.setState({
                                    goToPage:value
                                });


                                if ( this.state.goToPage !== '' && value  <= PagesCount ) {
                                    await this.setState({
                                        params:{
                                            ...this.state.params,
                                            page:value
                                        },
                                        first:( value-1 )*10
                                    });
                                }
                                const params  = this.returnParams();
                                this.props.getSubscribed( params )
                            }} value={ this.state.goToPage }/>
                            <div className='for_text'>for { PagesCount }</div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}


const mapStateToProps = state => {
    return {
        subscribed: state.subscribed
    };
};

const mapDispatchToProps = dispatch => {
    return {
        getSubscribed:  ( params ) => {
            dispatch(actions.getSubscribed( params ) )
        },
        initSbscribed:( data )=>{
            dispatch(actions.initSbscribed( data ) )
        },
        toExel:( data )=>{
            dispatch(actions.toExel(  ) )
        },



    };
};

export default connect( mapStateToProps, mapDispatchToProps )( Subscribed );
