import * as actionTypes from './actionTypes';
import {request} from '../actions/axios'

export const initSbscribed = (data) => {
    return {
        type: actionTypes.GET_SUBSCRIBED,
        data
    };
};
const toExelFile =(res)=>{
    var blob = new Blob([res.data], {
        type: 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet'
    });
    var objectUrl = URL.createObjectURL(blob);
    window.open(objectUrl);
}
export const toExel = () => dispatch => {
    return request.get(
        `/cms/users/toexel`,
    ).then(res => {
        toExelFile(res)
    }).catch(error => {
    });
};
export const getSubscribed = (params) => dispatch => {
    return request.get(
        `/cms/users/subscribed`,
        {params}
    ).then(res => {
        const response = res.data;
        if (response.success) {
            const suppliers = response.data.users;
            dispatch(initSbscribed({data: suppliers,count:res.data.data.queryResultCount, amount: response.data.amount}));
        }

    }).catch(error => {
    });
};
